@use "sass:math";

@import '../helpers/variables';
@import '../settings/typography';
@import '../settings/breakpoints';
@import './utilities';
@import './units';
@import './media-query';
@import './platform-query';

// Combine platform fluid font overrides with the default fluid fonts map
$itv-font-fluid-scale: recursive-map-merge($itv-fluid-fonts, $itv-platform-fluid-fonts);

// Returns a font-family.
// If font variation is available it will switch to variable fonts
@mixin itv-get-font-family() {
    font-family: zagma, Arial, sans-serif;
    font-weight: map-get($itv-font-weights, regular);

    @supports (font-variation-settings: normal) {
        font-family: 'zagma-vf', Arial, sans-serif;
    }
}


// Outputs a font-weight property and value.
//
// $font-weight     - Font-weight name.
//
// Compatibility untested.
@mixin get-font-weight($font-type, $font-weight) {
    $font-weight-value: map-get($itv-font-fluid-scale, $font-type, 'weight', $font-weight);

    @if map-has-key($itv-font-fluid-scale, $font-type, 'weight', $font-weight) {
        @if $font-weight == default {
            font-weight: map-get($itv-font-fluid-scale, $font-type, 'weight', $font-weight-value);
        } @else {
            font-weight: $font-weight-value;
        }
    } @else {
        @error "ERROR: Specified font-type(#{$font-type}) does not have font-weight(#{$font-weight})";
    }
}

@mixin get-font-style($font-type, $font-style) {
    $font-style-value: map-get($itv-font-fluid-scale, $font-type, 'style', $font-style);

    @if map-has-key($itv-font-fluid-scale, $font-type, 'style', $font-style) {
        @if $font-style == default {
            font-style: map-get($itv-font-fluid-scale, $font-type, 'style', $font-style-value);
        } @else {
            font-style: $font-style-value;
        }
    } @else {
        @error "ERROR: Specified font-type(#{$font-type}) does not have font-style(#{$font-style})";
    }
}

@mixin itv-font-type($font-type, $font-weight: default, $font-style: default) {
    $base-font-size: get-font-size('base'); // used to convert to rem
    $line-height-size: map-get($itv-font-fluid-scale, $font-type, 'height');
    $max-breakpoint: math.div(get-max-breakpoint(), $base-font-size);
    $min-breakpoint: math.div(get-min-breakpoint(), $base-font-size);
    $max-font-size: math.div(get-font-size($font-type, 'max'), $base-font-size);
    $min-font-size: math.div(get-font-size($font-type, 'min'), $base-font-size);
    $letter-spacing: map-get($itv-font-fluid-scale, $font-type, 'spacing');

    @include get-font-weight($font-type, $font-weight);

    @include get-font-style($font-type, $font-style);

    @if map-has-key($itv-font-fluid-scale, $font-type, 'case') {
        text-transform: map-get($itv-font-fluid-scale, $font-type, 'case');
    }

    letter-spacing: #{$letter-spacing}em;


    @include itv-platform-query(ctv) {
        // Set max font size
        font-size: #{$max-font-size}rem;
        line-height: #{$max-font-size * $line-height-size}rem;
    }

    @include itv-platform-query(browser) {
        // Set min font size
        font-size: #{$min-font-size}rem;
        line-height: #{$min-font-size * $line-height-size}rem;

        // Start fluid fonts at min breakpoint
        @media (min-width: #{$min-breakpoint}rem) {
            font-size: calc(
                #{$min-font-size}rem +
                (#{$max-font-size} - #{$min-font-size}) *
                ((100vw - #{$min-breakpoint}rem) / (#{$max-breakpoint} - #{$min-breakpoint}))
            );

            line-height: calc(
                #{$min-font-size * $line-height-size}rem +
                (#{$max-font-size * $line-height-size} - #{$min-font-size * $line-height-size}) *
                ((100vw - #{$min-breakpoint}rem) / (#{$max-breakpoint} - #{$min-breakpoint}))
            );
        }
    }
}
