@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

$ps: 'ProfilesScreen';

.#{$ps} {
    margin-top: 247px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__header {
        @include itv-font-type(large-title);
    }

    &__container {
        margin: $spacing-10 0;
        display: flex;
        gap: 84px;
    }

    &__profile-group {
        .#{$ps}__edit-icon {
            position: absolute;
            z-index: 2;
            padding-left: 120px;
            top: 0;
            width: 49px;
            height: 49px;

            &::before {
                content: "";
                width: 49px;
                height: 49px;
                position: inherit;
                background-color: $color-itvx-neutrals-300;
                opacity: $opacity-03;
                z-index: 1;
                border-radius: 50%;
            }
    
            svg {
                filter: drop-shadow(0 3px 3px rgba(0, 0, 0, 0.3));
            }
        }

        .ProfileButton__button.focus ~ .#{$ps}__edit-icon:before {
            opacity: 0;
        }
    }
}
